<template lang="html">
  <div class="token__request">
    <div class="token__request__img">
      <v-avatar
        size="100"
        color="grey lighten-4"
      >
        <img src="../../assets/img/dualipa.jpg" alt="avatar">
      </v-avatar>
    </div>
    <div class="token__request__user">
      <h4>{{ getProfile.fullname }}</h4>
      <h5>{{ getProfile.email }}</h5>
      <h5>Current Balances: {{ userToken.token_amount }}</h5>
    </div>
    <div class="token__request__req">
      <h3>Request Token</h3>
      <div class="token__request__req__amount">
        <div @click.prevent="decrement">-</div>
        <input v-model.number="tokenAmount" type="number" min="1">
        <div @click.prevent="increment">+</div>
        <!-- <span>Max</span> -->
      </div>
      <v-btn
        @click="changeDialogState"
        depressed
        small
        color="#2B89B9"
        style="color:#fff;"
      >
        Request
      </v-btn>
    </div>
    <TokenRequestModal :tokenAmount="tokenAmount" @clicked="snackbarValChild" />
  </div>
</template>

<script>
import TokenRequestModal from './TokenRequestModal'

export default {
  components: {
    TokenRequestModal
  },
  props: [
    'userToken'
  ],
  data () {
    return {
      tokenAmount: 1,
    }
  },
  methods: {
    increment () {
      this.tokenAmount++;
    },
    decrement () {
      if(this.tokenAmount > 1) {
        this.tokenAmount--;
      }
    },
    changeDialogState () {
      this.$store.commit('toggleDialog');
    },
    snackbarValChild(value) {
      this.$emit('clickedSnackbar', 'true')
    }
  },
  computed: {
    getProfile () {
      return this.$store.state.profile
    },
    showDialog () {
      return this.$store.state.showDialog
    }
  },
}
</script>

<style lang="scss">
</style>



