<template lang="html">
  <div class="">
    <content-loader
      v-if="!handleLoader"
  		:height="736"
  		:width="1024"
  		:speed="2"
  		primaryColor="#f3f3f3"
  		secondaryColor="#ecebeb"
  	>
  		<rect x="36.78" y="81.83" rx="0" ry="0" width="431.35" height="45.36" />
  		<rect x="549.9" y="81.83" rx="0" ry="0" width="428.67" height="45.36" />
  		<rect x="37.9" y="155.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="204.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="254.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="303.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="353.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="404.83" rx="0" ry="0" width="940" height="43.01" />
  	</content-loader>

    <div class="token" v-if="handleLoader">
      <div class="token--left">
        <div class="boxNotif">
           <v-snackbar
            v-model="handleSnackbarVal"
            :color="notifColor"
            :timeout="timeout"
            top
          >
            <p>{{ notifResMessage.message }}</p>
          </v-snackbar>
        </div>
        <TokenFilter @clickedSnackbar="snackbarValChild" :userToken="userToken" v-if="stateResizeScreen == 3 || stateResizeScreen == 2" />
        <TokenHeaderResp :userToken="userToken" v-if="stateResizeScreen == 1" />
        <TokenList :userTokenList="userTokenList" v-if="stateResizeScreen == 3 || stateResizeScreen == 2" />
        <TokenListResp :userTokenList="userTokenList" v-if="stateResizeScreen == 1" />
      </div>
    </div>
  </div>
</template>

<script>
import { ContentLoader } from 'vue-content-loader';
import TokenRequest from '../../student-components/token/TokenRequest'
import TokenFilter from '../../student-components/token/TokenFilter'
import TokenList from '../../student-components/token/TokenList'
import TokenNotif from '../../student-components/token/TokenNotif'
import TokenHeaderResp from '../../student-components/token/TokenHeaderResp'
import TokenListResp from '../../student-components/token/TokenListResp'

export default {
  components: {
    ContentLoader,
    TokenRequest,
    TokenFilter,
    TokenList,
    TokenNotif,
    TokenHeaderResp,
    TokenListResp
  },
  data: () => ({
    myData: null,
    timeout: 3000,
    windowWidth:0,
    windowHeigt:0,
    ResizeScreen: null
  }),
  mounted () {
    setTimeout(() => {
      this.myData = 'Example Data';
    }, 3000)
    // console.log(this.$store.state.DataAdditional)
    this.$nextTick( () => {
       window.addEventListener('resize', this.getWindowWidth);
       window.addEventListener('resize', this.getWindowHeight);

       this.getWindowWidth()
       this.getWindowHeight()
    });
  },
  methods: {
    getToken (event) {
      // return this.$store.state.UserToken
      const token = this.$store.state.UserToken
      console.log(token);
    },
    snackbarValChild(value) {
      return this.snackbar = value
    },
    getWindowWidth(event){
       this.windowWidth = document.documentElement.clientWidth;

       if (this.windowWidth <= 599){
       //this for resolution phone only
       const resizeScreen = 1
       this.$store.commit('commitResizeScreen', resizeScreen)
       }else if(this.windowWidth <= 768){
          //this for resolution Ipad Potrait
          const resizeScreen = 2
          this.$store.commit('commitResizeScreen', resizeScreen)
       }else if(this.windowWidth >= 1024){
          //this for resolution Dekstop-up
          const resizeScreen = 3
          this.$store.commit('commitResizeScreen', resizeScreen)
       }
    },
    getWindowHeight(event){
       this.windowHeigt = document.documentElement.clientHeight;
    }
  },
  computed: {
    stateResizeScreen () {
      const screen = this.$store.state.stateResizeScreen
      if (screen == null) {
        return 0
      } else {
        return screen
      }
    },
    userToken () {
      return this.$store.state.UserToken
    },
    userTokenList () {
      return this.$store.state.stateTokenFilterDate.data
    },
    handleLoader () {
      const data = this.userTokenList
      const data2 = this.userToken
      const data3 = this.$store.state.stateTokenStatus

      if (data == null || data2 == null || data3 == null) {
        return false
      } else {
        return true
      }
    },
    handleSnackbarVal () {
      return this.$store.state.stateSnackbarVal
    },
    notifResMessage() {
      const message = this.$store.state.stateSnackbarMessage
      if (message == null) {
        return "0"
      } else if (message !== null) {
        return message.data
      }
      // console.log(resCoins);
    },
    notifColor() {
      const resStatus = this.$store.state.stateSnackbarMessage

      if (resStatus == null) {
        return "none"
      } else if (resStatus.status == 200) {
        return "success"
      } {
        return "error"
      }
    }
  },
  created () {
    this.$store.dispatch("GET_COIN");
    this.$store.dispatch("GET_PROFILE_TOKEN_HISTORY")
    this.$store.dispatch("GET_COIN_STATUS")
  },
  watch: {
    handleSnackbarVal (val) {
      const stateSnackbarVal = false

      if (val == true) {
        setTimeout(() => (
          this.$store.commit("commitSnackbarVal", stateSnackbarVal)
        ), 3000)
      }
    },
  },
  beforeDestroy() {
     window.removeEventListener('resize', this.getWindowWidth);
     window.removeEventListener('resize', this.getWindowHeight);
  }
}
</script>

<style lang="scss">
  @import './Token.scss';
</style>



