<template lang="html">
  <div class="boxNotif">
     <v-snackbar
      v-model="snackbar"
      :color="notifColor"
      :timeout="timeout"
      top
    >
      {{ notifResMessage.message }}
      <v-btn
        dark
        flat
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  data () {
    return {
      snackbar: false,
      timeout: 3000,
    }
  },
  computed: {
    notifRes() {
      return this.$store.getters.CHECK_CONFIRM_TOKENS
    },
    notifResMessage() {
      return this.$store.state.reqToken
      // console.log(resCoins);
    },
    notifColor() {
      const resStatus = this.$store.state.reqToken

      if (resStatus.status == 200) {
        return "success"
      } else {
        return "error"
      }
    }
  }
}
</script>

<style lang="css">
</style>



