<template lang="html">
  <v-dialog width="300" v-model="showDialog" persistent>
     <v-card style="background-color:#fff;border-radius: 3px;">
       <div class="flex--col--center dialog__content" v-if="whichRequestCancel == 'request'">
         <span>Are you sure want to request token?</span>
         <div class="modal__action--btn">
           <div
             style="color:#3B3B3B;padding:10px;margin:0 10px 0 0;cursor:pointer;"
             @click="changeDialogState">
             Cancel
           </div>
           <v-btn
             depressed
             small
             color="#375197"
             style="color:#fff;"
             @click="reqToken">
             Confirm
           </v-btn>
         </div>
       </div>
       <div class="flex--col--center dialog__content" v-if="whichRequestCancel == 'cancel'">
         <span>Are you sure want to cancel token request?</span>
         <div class="modal__action--btn">
           <div
             style="color:#3B3B3B;padding:10px;margin:0 10px 0 0;cursor:pointer;"
             @click="changeDialogState">
             Cancel
           </div>
           <v-btn
             depressed
             small
             color="#375197"
             style="color:#fff;"
             @click="cancelTokenRequest">
             Confirm
           </v-btn>
         </div>
       </div>
     </v-card>
   </v-dialog>
</template>

<script>
import axios from '../../store/axios-auth'

export default {
  props: [
    'tokenAmount',
    'whichRequestCancel'
  ],
  methods: {
    changeDialogState () {
      this.$store.commit('toggleDialog');
    },
    getCointStatus () {
      this.$store.dispatch("GET_COIN_STATUS");
      this.$store.dispatch("GET_NOTIFICATIONS_LATEST");
      this.$store.commit('muttShowBadges',true)
    },
    reqToken () {
      const tokenAmount = {
        token_amount: this.tokenAmount
      }
      this.$store.dispatch('POST_COIN', tokenAmount);
      setTimeout(this.getCointStatus, 1000);
      // console.log(tokenAmount);
      this.$store.commit('toggleDialog');
      this.$emit('clicked', 'true')
      this.$emit('zero', 0)
    },
    cancelTokenRequest () {
      this.$store.dispatch("PUT_CANCEL_REQUEST");
      setTimeout(this.getCointStatus, 1000);
      this.$store.commit('toggleDialog');
      this.$emit('clicked', 'true')
    },
  },
  computed: {
    showDialog () {
      return this.$store.state.showDialog
    },
  }
}
</script>

<style lang="scss">
  .dialog__content {
    padding: 30px 0;
  }
</style>



