<template lang="html">
  <div class="token__resp">
    <div class="token__resp--header">
      <div class="token__resp--left">
        <h2>Token History</h2>
        <!-- <div class="">
        </div> -->
        <span @click="toggleFilter">Filter By Date <v-icon size="10" color="#375197">fa-chevron-down</v-icon></span>
      </div>
      <div class="token__resp--right">
        <h2>Token Balances:</h2>
        <h1>{{ userToken.token_amount }}</h1>
        <div class="btn__border--darkblue" @click="changeDialogState('request')" v-if="handleTokenStatus">
          Request Token
        </div>
        <div class="btn__border--darkblue" @click="changeDialogState('cancel')" v-if="!handleTokenStatus">
          Cancel Request
        </div>
      </div>
    </div>
    <!-- dialog for request token -->
    <v-dialog width="230" v-model="showDialog" persistent>
       <v-card style="background-color:#fff;border-radius: 3px;">
         <div class="flex--col--center dialog__content--resp" v-if="whichRequestCancel == 'request'">
           <h1>Request Token</h1>
           <div class="token__request__req__amount">
             <div @click.prevent="decrement">-</div>
             <input v-model.number="tokenAmount" type="number" min="1">
             <div @click.prevent="increment">+</div>
             <!-- <span>Set Max</span> -->
           </div>
           <div class="modal__action--btn">
             <div
               style="color:#3B3B3B;padding:10px;margin:0 10px 0 0;cursor:pointer;"
               @click="changeDialogState">
               Cancel
             </div>
             <v-btn
               depressed
               small
               color="#375197"
               style="color:#fff;"
               @click="reqToken">
               Confirm
             </v-btn>
           </div>
         </div>
         <div class="flex--col--center dialog__content--resp" v-if="whichRequestCancel == 'cancel'">
           <h2 style="font-weight:600;">Cancel Request</h2>
           <div class="modal__action--btn">
             <div
               style="color:#3B3B3B;padding:10px;margin:0 10px 0 0;cursor:pointer;"
               @click="changeDialogState">
               Cancel
             </div>
             <v-btn
               depressed
               small
               color="#375197"
               style="color:#fff;"
               @click="cancelTokenRequest">
               Confirm
             </v-btn>
           </div>
         </div>
       </v-card>
     </v-dialog>
     <!-- dialog for request token -->
    <div class="token__resp--filter" v-if="handleFilter">
      <div class="" style="display:flex;align-items:center;">
        <label>From</label>
        <div class="datepicker--border">
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :nudge-right="40"
            lazy
            transition="scale-transition"
            offset-y
            full-width
            min-width="290px"
          >
            <v-text-field
              slot="activator"
              v-model="start_time"
              append-icon="event"
              @click="togleDatepicker"
            ></v-text-field>
            <v-date-picker
              v-model="start_time"
              v-if="menu"
              no-title
              :min="minDate"
            >
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
              <v-btn flat color="primary" @click="menu = false">OK</v-btn>
            </v-date-picker>
          </v-menu>
        </div>
        <label>To</label>
        <div class="datepicker--border">
          <v-menu
            ref="menu"
            v-model="menu2"
            :close-on-content-click="false"
            :nudge-right="40"
            lazy
            transition="scale-transition"
            offset-y
            full-width
            min-width="290px"
          >
            <v-text-field
              slot="activator"
              v-model="end_time"
              append-icon="event"
              @click="togleDatepicker2"
            ></v-text-field>
            <v-date-picker
              v-model="end_time"
              v-if="menu2"
              no-title
              :min="start_time"
            >
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="menu2 = false">Cancel</v-btn>
              <v-btn flat color="primary" @click="menu2 = false">OK</v-btn>
            </v-date-picker>
          </v-menu>
        </div>
      </div>
      <div class="" style="display:flex;justify-content:center;">
        <v-btn
          class="btn--33"
          depressed
          small
          color="#375197"
          style="color:#fff;"
          @click="filterDate"
        >
        Apply
        </v-btn>
        <v-btn
          class="btn--33"
          depressed
          small
          color="#375197"
          style="color:#fff;"
          @click="resetFilter"
        >
        Reset
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: [
    'userToken'
  ],
  data: () => ({
    menu: false,
    menu2: false,
    start_time: null,
    end_time: null,
    handleFilter: false,
    tokenAmount: 0,
    whichRequestCancel: null
  }),
  computed: {
    showDialog () {
      return this.$store.state.showDialog
    },
    getTokenStatus () {
      const data = this.$store.state.stateTokenStatus

      if (data.data == null) {
        return 0
      } else {
        return data
      }
    },
    handleTokenStatus () {
      const data = this.getTokenStatus

      if (data == 0 || data.data.status == 'cancelled') {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    changeDialogState (i) {
      this.whichRequestCancel = i
      this.$store.commit('toggleDialog');
    },
    toggleFilter () {
      this.handleFilter = !this.handleFilter
    },
    togleDatepicker () {
      this.menu = !this.menu
    },
    togleDatepicker2 () {
      this.menu2 = !this.menu2
    },
    filterDate () {
      const rangeDate = {
        start_time: this.start_time,
        end_time: this.end_time
      }

      this.$store.dispatch("POST_TOKEN_FILTER_DATE", rangeDate)
    },
    resetFilter () {
      this.$store.dispatch("GET_PROFILE_TOKEN_HISTORY")
      this.start_time = null
      this.end_time = null
    },
    increment () {
      this.tokenAmount += 2;
    },
    decrement () {
      if(this.tokenAmount > 2) {
        this.tokenAmount -= 2;
      }
    },
    getCointStatus () {
      this.$store.dispatch("GET_COIN_STATUS");
    },
    reqToken () {
      const tokenAmount = {
        token_amount: this.tokenAmount
      }
      this.$store.dispatch('POST_COIN', tokenAmount);
      setTimeout(this.getCointStatus, 1000);
      this.$store.commit('toggleDialog');
      this.tokenAmount = 0
    },
    cancelTokenRequest () {
      this.$store.dispatch("PUT_CANCEL_REQUEST");
      setTimeout(this.getCointStatus, 1000);
      this.$store.commit('toggleDialog');
      this.$emit('clicked', 'true')
    },
  }
}
</script>

<style lang="css">
</style>



