<template lang="html">
  <div>
    <v-icon size="10" color="#375197" v-if="!toggleDetail"  @click="toggleDetails">fa-chevron-down</v-icon>
    <div class="token__resp--list-ellapsed" v-if="toggleDetail">
      <div class="desc--title">Description</div>
      <div class="desc--detail">{{ data.description }}</div>
      <div class="status--title">Status</div>
      <div class="status--detail">{{ data.token_status }}</div>
      <div class="transaction--title">Token Transaction</div>
      <div class="transaction--detail">{{ data.balance - data.pre_balance }}</div>
    </div>
    <v-icon size="10" color="#375197" v-if="toggleDetail" @click="toggleDetails">fa-chevron-up</v-icon>
  </div>
</template>

<script>
export default {
  props: ['data'],
  data: () => ({
    toggleDetail: false
  }),
  methods: {
    toggleDetails () {
      this.toggleDetail = !this.toggleDetail
    }
  }
}
</script>

<style lang="css">
</style>



