<template lang="html">
  <div class="table__list">
    <table style="width:100%">
      <tr>
        <th>Date</th>
        <th>Description</th>
        <th>Status</th>
        <th>Token Transaction</th>
      </tr>
      <tr v-for="(data, i) in sortedItems">
        <td align="center">{{ data.transaction_date | formatDate }}</td>
        <td align="center">{{ data.description }}</td>
        <td align="center" style="text-transform:capitalize">{{ data.token_status }}</td>
        <td><div :class="data.token_status">{{ data.balance - data.pre_balance }}</div></td>
      </tr>
    </table>
  </div>
</template>

<script>
import { MomentFilter } from '../../components/mixins/MomentFilter.js'

export default {
  mixins: [MomentFilter],
  props: [
    "userTokenList"
  ],
  data () {
    return {

    }
  },
  computed: {
    sortedItems() {
      function compare(a, b) {
        if (a.id < b.id)
          return 1;
        if (a.id > b.id)
          return -1;
        return 0;
      }

      return this.userTokenList.sort(compare);
    },
  },
  methods: {

  }
}
</script>

<style lang="scss">
</style>



