<template lang="html">
  <div class="token__resp--list">
    <div class="token__resp--list-each" v-for="(data, i) in sortedItems" :key="i">
      <div class="token__resp--list-collapsed">
        <div class="token__resp--list-left">
          <div class="token__resp--list-desc overflow--ellipsis">{{ data.description }}</div>
          <div class="token__resp--list-date">{{ data.transaction_date }}</div>
        </div>
        <div class="token__resp--list-right">
          <div class="token__resp--list-status" :class="data.token_status">{{ data.token_status }}</div>
          <div class="token__resp--list-amount" :class="data.token_status">{{ data.balance - data.pre_balance }}</div>
        </div>
      </div>
      <TokenListToggle :data="data" />
    </div>
  </div>
</template>

<script>
import TokenListToggle from './TokenListToggleResp.vue'

export default {
  components: {
    TokenListToggle
  },
  props: [
    "userTokenList"
  ],
  data: () => ({

  }),
  computed: {
    sortedItems() {
      function compare(a, b) {
        if (a.id < b.id)
          return 1;
        if (a.id > b.id)
          return -1;
        return 0;
      }

      return this.userTokenList.sort(compare);
    },
  },
  methods: {

  }
}
</script>

<style lang="css">
</style>



