<template lang="html">
  <div class="">
    <div class="token__header">
      <div class="token__header--left">
        <div class="view__header__title">
          <div>
            <h2>Token History</h2>
            <div class="blue__circle"></div>
          </div>
        </div>
        <div class="view__header__filter">
          <span>Filter sessions by date range</span>
          <div class="header__filter">
            <label>From</label>
            <div class="datepicker--border">
              <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                :nudge-right="40"
                lazy
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <v-text-field
                  slot="activator"
                  v-model="start_time"
                  append-icon="event"
                  @click="togleDatepicker"
                ></v-text-field>
                <v-date-picker
                  v-model="start_time"
                  v-if="menu"
                  no-title
                >
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="menu = false">OK</v-btn>
                </v-date-picker>
              </v-menu>
            </div>
            <label>To</label>
            <div class="datepicker--border">
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                :nudge-right="40"
                lazy
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <v-text-field
                  slot="activator"
                  v-model="end_time"
                  append-icon="event"
                  @click="togleDatepicker2"
                ></v-text-field>
                <v-date-picker
                  v-model="end_time"
                  v-if="menu2"
                  no-title
                  :min="start_time"
                >
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="menu2 = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="menu2 = false">OK</v-btn>
                </v-date-picker>
              </v-menu>
            </div>
            <v-btn
              class="btn--33"
              depressed
              small
              color="#375197"
              style="color:#fff;"
              @click="filterDate"
            >
            Apply Filter
            </v-btn>
            <v-btn
              class="btn--33"
              depressed
              small
              color="#375197"
              style="color:#fff;"
              @click="resetFilter"
            >
            Reset Filter
            </v-btn>
            <!-- <div class="trans__btn--quarterround filter--btn" @click="resetFilter">Reset Filter</div> -->
          </div>
        </div>
      </div>
      <div class="token__header--right">
        <div class="token__balance--student">
          <h2>Token Balances: {{ userToken.token_amount }}</h2>
        </div>
        <div class="token__request__req">
          <h4>Request Tokens</h4>
          <div class="token__request__req--form">
            <div class="token__request__req__amount">
              <div @click.prevent="decrement">-</div>
              <input v-model.number="tokenAmount" type="number" min="0">
              <div @click.prevent="increment">+</div>
              <!-- <span>Set Max</span> -->
            </div>
            <v-btn
              @click="changeDialogState('request')"
              depressed
              small
              color="#375197"
              style="color:#fff;margin:0;"
              v-if="handleTokenStatus"
            >
              Request
            </v-btn>
            <v-btn
              @click="changeDialogState('cancel')"
              depressed
              small
              color="#375197"
              style="color:#fff;margin:0;"
              v-if="!handleTokenStatus"
            >
              Cancel
            </v-btn>
          </div>
        </div>
      </div>
    </div>
    <TokenRequestModal :whichRequestCancel="whichRequestCancel" :tokenAmount="tokenAmount" @clicked="snackbarValChild" @zero="toZeroTokenAmount" />
  </div>
</template>

<script>
import { DateRange } from 'vuetify-daterange-picker';
import 'vuetify-daterange-picker/dist/vuetify-daterange-picker.css';
import { format, subDays, addDays } from 'date-fns';
import TokenRequestModal from './TokenRequestModal'

export default {
  components: {
    [DateRange.name]: DateRange,
    TokenRequestModal
  },
  props: [
    'userToken'
  ],
  data () {
    return {
      range: [],
      menu: false,
      menu2: false,
      start_time: null,
      end_time: null,
      tokenAmount: 0,
      whichRequestCancel: null
    }
  },
  mounted () {

  },
  computed: {
    showDialog () {
      return this.$store.state.showDialog
    },
    getTokenStatus () {
      const data = this.$store.state.stateTokenStatus

      if (data.data == null) {
        return 0
      } else {
        return data
      }
    },
    handleTokenStatus () {
      const data = this.getTokenStatus

      if (data == 0 || data.data.status == 'cancelled') {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    onDateRangeChange(range) {
      this.range = range;
    },
    increment () {
      this.tokenAmount+=2;
    },
    decrement () {
      if(this.tokenAmount > 0) {
        this.tokenAmount -= 2;
      }
    },
    changeDialogState (i) {
      this.$store.commit('toggleDialog');
      this.whichRequestCancel = i
      // console.log(i);
    },
    snackbarValChild(value) {
      this.$emit('clickedSnackbar', 'true')
    },
    toZeroTokenAmount(value) {
      this.tokenAmount = value
    },
    togleDatepicker () {
      this.menu = !this.menu
    },
    togleDatepicker2 () {
      this.menu2 = !this.menu2
    },
    filterDate () {
      const rangeDate = {
        start_time: this.start_time,
        end_time: this.end_time
      }

      this.$store.dispatch("POST_TOKEN_FILTER_DATE", rangeDate)
    },
    resetFilter () {
      this.$store.dispatch("GET_PROFILE_TOKEN_HISTORY")
      this.start_time = null
      this.end_time = null
    }
  },
}
</script>

<style lang="scss">

</style>



